import { Component, Input, OnInit } from '@angular/core';
import { Employee } from 'src/app/Model/employee';
import { HttpaddemployeeService } from 'src/app/service/httpaddemployee.service';

@Component({
  selector: 'app-update-employee',
  templateUrl: './update-employee.component.html',
  styleUrls: ['./update-employee.component.css']
})
export class UpdateEmployeeComponent implements OnInit {

@Input() parentdata=<Employee>{}
allCountry:any[]= []; 
issubmitDissabled:boolean=true;
backendResponse="";

  constructor(private empservice:HttpaddemployeeService) { }

  

  ngOnInit() {
    this.getallCountry();
  }

  // Getting all country at the time of creating instance
 getallCountry(){
    this.empservice.getallCountryBackend()
    .subscribe((response)=>{
      //  console.log(response);
       this.allCountry=(<any>response);
    })
  }

  onupdate(){
   
    let empid= this.parentdata.id;
   console.log(this.parentdata);
    this.empservice.updateEmployeeBackend(empid, this.parentdata)
    .subscribe((response)=>{
      console.log(response);
      this.backendResponse=response;
      this.issubmitDissabled=false;
    })


  }



}
