import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class HttpaddemployeeService {

  // private baseUrl="http://localhost:8080/"; 
  private baseUrl=environment.BaseUrl;

  constructor(private http:HttpClient) { }

 //to fetch all country&show in list
 getallCountryBackend(){
  return  (this.http.get((`${this.baseUrl}getallcountry`)));
 }

 // to add employee
 addEmployeeBackend(empObj:any){
  return (this.http.post((`${this.baseUrl}addEmployee`),empObj));
}

//update employee
updateEmployeeBackend(empId:number, parentdata:any){
  return (this.http.put(`${this.baseUrl}updateEmployee/${empId}`,parentdata, {responseType:'text'}));
}



}
