import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpaddemployeeService } from 'src/app/service/httpaddemployee.service';

@Component({
  selector: 'app-addemployee',
  templateUrl: './addemployee.component.html',
  styleUrls: ['./addemployee.component.css']
})
export class AddemployeeComponent implements OnInit {

  allCountry:any[]=[];

  constructor(private addempservice:HttpaddemployeeService, private router:Router) { }

  ngOnInit() {
    this.getallCountry();
  }


  getallCountry(){
    this.addempservice.getallCountryBackend()
    .subscribe((response)=>{
       console.log(response);
       this.allCountry=(<any>response);
    })
  }

   //reset the form
   resetForm(f){
    f.resetForm();
  }

   //Submit te add employee form
  onSubmit(f:NgForm){
    let empObj={
      name: f.value.name,
      email:f.value.email,
      designation: f.value.designation,
      status:f.value.status,
      phoneno:f.value.phoneno,
      country:{
        cid:f.value.country.cid,
        cname: f.value.country.cname
      },
      createdtm: Date.now(),
      createdby: sessionStorage.getItem("username"),
      updatedtm: Date.now(),
      updatedby: sessionStorage.getItem("username"),
      doj: new Date("Fri Dec 08 2019 07:44:57")
      
    }
    this.addempservice.addEmployeeBackend(empObj)
    .subscribe((response)=>{
      console.log(response);
      this.router.navigate(['/home'])

    })

  }
}
