import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddemployeeComponent } from './component/addemployee/addemployee.component';
import { EmpdetailComponent } from './component/empdetail/empdetail.component';
import { HomeComponent } from './component/home/home.component';
import { LoginComponent } from './component/login/login.component';
import { NotfoundComponent } from './component/notfound/notfound.component';
import { AuthGuard } from './guardservice/auth.guard';


const routes: Routes = [
  {
    path:"", redirectTo:"/login", pathMatch: "full"
  },
  {
    path:"login", component:LoginComponent, pathMatch:"full"
  },
  {
    path:"home", component:HomeComponent, pathMatch:"full", canActivate:[AuthGuard]
  },
  {
    path:"empDetails/:id", component:EmpdetailComponent, pathMatch: "full", canActivate:[AuthGuard]
  },
  {
    path:"addEmployee", component:AddemployeeComponent, pathMatch: "full", canActivate:[AuthGuard]
  },
  {
    path:"**", component:NotfoundComponent
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
