//Method to submit form
onSubmit(f: any) {
  let credentials = {
    email: f.value.email,
    password: f.value.password,
  }
  this.httplogin.loginCheck(credentials)
    .subscribe((response) => {
      this.loginObj = (<any>response);
      
      console.log("======>" + this.loginObj.message);
      sessionStorage.setItem("username", this.loginObj.user.username)
      this.router.navigate(["/home"]);


    }, (error) => {
      this.incorrect="Invalid credentials";
      
     

    });

}
  
