import { Component, OnInit, TemplateRef } from '@angular/core';
import { HttphomeService } from 'src/app/service/httphome.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { Employee } from 'src/app/Model/employee';
import { DialogService } from 'src/app/service/dialog.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  EMPdata:any[]=[];
  radioCheck:boolean=false;
  modalRef:BsModalRef;
  empobj=<Employee>{}
  p:number=1;
  nameSearch:string="";

  config={
    animated: true,
    ignoreBackdropClick:false,
    class: "alert alert-danger"

  }

  constructor(private homeservice:HttphomeService
    ,private modalservice:BsModalService
    ,private dialogserice:DialogService
    ,private toastr:ToastrService) { }

  ngOnInit() {
    this.getallemp();
  }

    //method to get all empoloyess
    getallemp(){
      this.homeservice.getAllEmp()
      .subscribe((response)=>{
        console.log(response);
        this.EMPdata=(<any>response);
        console.log(this.EMPdata)
      })
    }



  //To update Employee
  onUpdate(popUp:TemplateRef<any>){
    if(this.onRadioCheck()){
      //show update component
      this.modalRef=this.modalservice.show(popUp, this.config);

    }
    else{
      //if radio not selected
      alert("Please select the employee to update!!");
    }

  }

  //on radio click getting current item 
  onRadioClick(item:any){
    this.radioCheck=true;
    console.log(item);
    this.empobj=item;

  }

  //method to check radio is selected or not
  onRadioCheck(){
    if(this.radioCheck){
      return true;
    }
    else{
      return false;
    }
  }

  // To delete the employee
  onDelete(){
    if(this.onRadioCheck()){
      //Process to delet the emp
      this.dialogserice.OpenConfirmDialog("Are you sure you want to delete this Employee?")
      .afterClosed()
      .subscribe((res)=>{
        if(res){
          //Delete the employee
          this.homeservice.deleteEmployeeBackend(this.empobj.id)
          .subscribe((response)=>{
            // console.log(response);
            this.toastr.success(response, 'Delete Notificaton');
            this.getallemp();
          })

        }
        else{
          //Don't delete the employee
          
        }
      })

    }
    else{
      //if radio not selected
      alert("Please select the employee to delete!!");
    }

  }
}
