import { Component, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Employee } from 'src/app/Model/employee';
import { HttpempdetailService } from 'src/app/service/httpempdetail.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-empdetail',
  templateUrl: './empdetail.component.html',
  styleUrls: ['./empdetail.component.css']
})
export class EmpdetailComponent implements OnInit {
  modalRef:BsModalRef;
  EMPid:number;
  EMPObject=<Employee>{}
  config={
    animated: true,
    ignoreBackdropClick:true,
    class: "alert alert-danger"

  }
  constructor(private route:ActivatedRoute, private http:HttpempdetailService,private modalservice:BsModalService) { }

  ngOnInit() {
    this.getAllDataFromURL();
    this.getEMPdataFromBackend(this.EMPid);
  }

  getAllDataFromURL() {
    this.route.paramMap
      .subscribe((param) => {
        this.EMPid = +param.get("id");
        console.log(this.EMPid);
      })
    }

    getEMPdataFromBackend(EMPid:number){
      this.http.getEmpById(EMPid)
      .subscribe((response)=>{
        console.log(response);
        this.EMPObject=(<Employee>response);
        console.log(this.EMPObject);
        
      })
  

    }  

    OnUpdate(popUp:TemplateRef<any>){
      this.modalRef=this.modalservice.show(popUp, this.config);
    }



}
